<template>
  <div class="other">
    <van-nav-bar
        title="向管理员提问"
        fixed
        left-text="返回"
        left-arrow
        @click-left="$router.back()"
    />
    <section class="answer">
      <div class="top">
        <van-field maxlength="20" placeholder="请输入问题标题" v-model="questionInfo.title" label="问题标题" />
        <van-field
            v-model="questionInfo.content"
            rows="2"
            autosize
            label="问题描述"
            type="textarea"
            maxlength="1000"
            placeholder="请输入问题描述"
            show-word-limit
        />
        <div class="submit">
          <van-button style="width: 100%;"  @click="addQuestion">提问题</van-button>
        </div>
      </div>
      <div class="answer-list">
        <ul>
          <li v-for="item in answerList" :key="item.questionInfo.id">
            <div class="top">
              <van-button v-if="userInfo.id === item.userInfo.id" style="position: absolute;right: 1rem;top: 2.3rem;" type="danger" size="mini" @click="delQuestion(item.questionInfo.id)">删除</van-button>
              <div class="left">
                <Avatar icon="ios-person" size="small" :src="item.userInfo.avatar" />
              </div>
              <div class="right">
                <div class="title ellipsis">
                  <svg t="1579084662468" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="8640" width="200" height="200"><path d="M512.6 512.5m-447.4 0a447.4 447.4 0 1 0 894.8 0 447.4 447.4 0 1 0-894.8 0Z" fill="#27ADCC" p-id="8641"></path><path d="M512.2 807.1c-75.2 0-137.7 54.6-149.9 126.3 46.9 16.7 97.3 25.7 149.9 25.7s103-9.1 149.9-25.7c-12.3-71.7-74.7-126.3-149.9-126.3zM554.9 749.8h-87.5v-43.7c0-25.4 5.1-48.1 15.4-67.9 10.2-19.9 29.8-43.1 58.6-69.8 28.9-26.7 48.4-47.8 58.6-63.3 10.2-15.5 15.4-33.2 15.4-53.1 0-29.2-9.3-52.3-27.9-69.3-18.6-17.1-43.7-25.6-75.4-25.6-54 0-90.6 29.8-109.8 89.3l-80-33.5c14.9-41 39.1-74.1 72.6-99.6 33.5-25.4 72.6-38.2 117.3-38.2 55.8 0 101.6 16.6 137.3 49.8 35.7 33.2 53.5 75.5 53.5 127 0 26.1-6.5 51-19.5 74.9-13 23.9-37.5 52.3-73.5 85.2-36 32.9-54.3 65.5-54.9 97.7v40.1z" fill="#FFFFFF" p-id="8642"></path></svg>{{item.questionInfo.title}}
                </div>
                <div class="info">
                  <span>{{item.userInfo.username || '未知'}}</span><span>发起了提问</span><span>·</span><span>{{item.questionInfo.createdAt}}</span>
                </div>
              </div>
            </div>
            <div class="content">
              {{item.questionInfo.content}}
            </div>
            <div class="children">
              <ul>
                <li v-for="answer in item.answerChildren" :key="answer.answerInfo.id">
                  <div class="content"><van-tag v-if="answer.role === 1" type="success">管理员（{{answer.adminInfo.username}}）</van-tag><van-tag v-else type="primary">用户（{{answer.userInfo.username || '未知'}}）</van-tag>：{{answer.answerInfo.content}}</div>
                </li>
              </ul>
            </div>
          </li>
        </ul>
        <van-divider dashed>{{answerList.length === 0 ? '暂无相关答疑' : '已经到底了'}}</van-divider>
      </div>
    </section>
  </div>
</template>

<script>
import config from './../../../config'
import {addQuestion, getMyQuestionList, delQuestion} from "@/api";
import {Toast, Dialog} from "vant";
import {mapState} from "vuex";
import moment from "moment";
export default {
  name: "question",
  data(){
    return{
      answerList: [],
      questionInfo: {
        title:'',
        content: ''
      },
      answerInfo:{
        content: ''
      }
    }
  },
  mounted() {
    this.getMyQuestionList()
  },
  computed: {
    ...mapState(['userInfo'])
  },
  methods: {
    delQuestion(id){
      Dialog.confirm({
        title: '删除提问',
        message: '确认删除此提问吗？',
        confirmButtonText: '立即删除'
      }).then(async () => {
        let data = await delQuestion({
          id
        });
        if(data.code === 200){
          Toast(data.data);
          this.getMyQuestionList();
        }else {
          Toast(data.data);
        }
      }).catch(() => {});
    },
    async addQuestion(){
      let userId = config.getCache('userId');
      if(!userId){
        return Dialog.confirm({
          title: '尚未登录',
          message: '登录之后才可以提问题？',
          confirmButtonText: '前往登录'
        }).then(() => {
          this.$router.push('/user/login');
        }).catch(() => {});
      }
      let {title, content} = this.questionInfo;
      if(!title){
        return Toast('标题不得为空');
      }
      if(!content){
        return Toast('内容不得为空');
      }
      let data = await addQuestion({
        userId,
        title, content
      });
      if(data.code === 200){
        this.questionInfo = {
          title:'',
          content: ''
        };
        Toast(data.data);
        this.getMyQuestionList();
      }else {
        Toast(data.data);
      }
    },
    async getMyQuestionList(){
      let userId = config.getCache('userId');
      if(!userId){
        return Dialog.confirm({
          title: '尚未登录',
          message: '登录之后才可以提问题？',
          confirmButtonText: '前往登录'
        }).then(() => {
          this.$router.push('/user/login');
        }).catch(() => {});
      }
      let data = await getMyQuestionList({
        userId,
      });
      if(data.code === 200){
        // eslint-disable-next-line no-console
        console.log(data.result);
        this.answerList = data.result.answerList.map(i => {
          i.questionInfo.createdAt = moment(i.questionInfo.createdAt).format('YY/MM/DD HH:mm')
          return i
        });
      }else {
        Toast(data.data);
      }
    },
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
>>>.van-nav-bar{
  z-index 2 !important
}
.answer{
  width: 100%
  background-color: #f9f9f9
  >.answer-list{
    padding: 1rem
    margin-top: 2rem
    background-color: #fff
    >ul{
      width: 100%
      >li{
        width: 100%
        padding: 1rem
        border-bottom: .1rem solid #f1f1f1
        >.top{
          position relative
          width: 100%
          height: 5rem
          overflow: hidden;
          display flex
          >.left{
            max-width 3rem
            min-width 3rem
            flex 2
            display flex
            align-items: center;
            justify-content center
          }
          >.right{
            flex 8
            max-width 30rem
            padding: 0 0 0 .5rem
            >.title{
              color #616161
              font-size: 1.5rem
              height: 2.5rem
              >svg{
                position relative
                top: .2rem
                margin: 0 .5rem
                width: 1.5rem
                height: 1.5rem
              }
            }
            >.info{
              padding-left: .5rem
              >span{
                margin-right: 1rem
                color #919191
                font-size: 1.2rem
              }
            }
          }
        }
        >.content{
          padding-left: 4rem
        }
        >.children{
          padding-left: 4rem
          >ul{
            width: 100%
            >li{
              width: 100%
              background-color: #f9f9f9
              border-bottom .1rem solid #f9f9f9
              padding: .5rem
              font-size: 1.3rem
            }
          }
        }
      }
    }
  }
  position relative
  >.top{
    background-color: #fff
    >.submit{
      width: 100%
      height: 6rem
      position relative
    }
  }
}
</style>
